@import "./reset.css";
body{
    background-color: rgb(248, 248, 249);
    font-size: 20px;
}
*{
    box-sizing: border-box;
}

.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #AEAEAE;
    padding:15px 20px;
}
.button_list{
    display: flex;
    column-gap: 15px;
}
.button{
    width: 80px;
    height: 40px;
    padding: 5px 25px;
    background-color: grey;
    border-radius: 5px;
    cursor:auto;
   display: flex;
   justify-content: center;
}
.btn_active {
    background-color: hsla(0, 0%, 100%, 0.886);
    cursor: pointer;
}

.button img{
    display: block;
    height: 100%;
}
.searchBox{

}
.searchBox > input{
    
    padding: 5px 5px 5px 40px;
    height: 40px;
    background: url("../../public/img/searchbutton.png") no-repeat 5px 5px;
    background-size: auto 80%;
    background-color: hsla(0, 0%, 100%, 0.886);
    border-radius: 5px;
    
}

.main{
    display: flex;
    column-gap: 20px;

    
}
.sideBar{
    min-width: 200px;
    display: inline-block;
    width: 25vw;
    height: 88vh;
    overflow-y: scroll;
    
}
.listitem{
    padding: 10px 10px;
    border: 3px solid #C5C4C1;
    border-top: none;
}

.listitem h5{
    font-weight: bold;
}
.listitem.active{
    background-color: #C5C5C5;
}
.footer_item{
    display: flex;
    column-gap: 5px;
    flex-wrap: wrap;
}
.time_item{

}
.text_item{

}

.workSpace{
    width: 75vw;
    padding: 10px;
    
}
.last_time_update{
    text-align: center;
    color: #AEAEA8;
    margin-bottom: 10px;

}
.work_text{
    width: 100%;
    height: 95%;
    border: none;
    overflow: auto;
    outline: none;

    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
}


@media (max-width: 600px) {
    .header{
        flex-direction: column;
        row-gap: 15px;
    } 
    .sideBar{
        width:100%;
        display: flex;
        overflow-y: auto;
        overflow-x: scroll;
        height: 100%;
    }
    .listitem{
        min-width:50vw;
    }
    .main{
        flex-direction: column;
    }
    .workSpace{
        width: 100vw;
    }
    .work_text{
        height: 60vh;
    }
    .button{
        width: 28.0vw;
    }
   
    .searchBox > input{
        width:90vw
    }
}



